export default {
	name: "ygcComment",
	//属性
	props: {
		placeholder: {
			type: String
		}
	},
	data() {
		return {
			maskState: 0,
			content: '',
			platform: ''
		};
	},
	created() {
		this.platform = uni.getSystemInfoSync().platform;//1.0.2版新增更新解决安卓系统首次弹出的一点bug
	},
	methods: {
		stopPrevent(){},
		toggleMask(type){
			let	state = type === 'show' ? 1 : 0;
			this.maskState = state;
		},
		pubComment() {
			var content=this.content;
			this.content='';
			this.$emit('pubComment',content);
		}
	}
}
